.nav {
  position: fixed;
  z-index: 1000;
  width: 100%;
  display: flex;
  height: 5rem;
  justify-content: flex-end;
  align-items: center;
  transition: all 400ms linear;
}

.nav-items {
  display: flex;
  flex-basis: 35%;
  justify-content: space-evenly;
}

.nav-items > div > a {
  text-decoration: none;
  color: #222;
}

.nav-items > div {
  background: #f1f1f1;
  padding: 0.5rem 0.6rem;
  border-radius: 10px;
  color: #222;
  font-size: 1rem;
  font-family: var(--homefont);
  font-weight: 800;
  filter: drop-shadow(-2px 4px 1px #585858);
}

.nav-button {
  display: none;
}

.toggle-nav {
  background: none;
  padding: 0;
  border: none;
  color: #fff;
  font-size: 1.25rem;
}
