.profile {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40rem;
}

.profile > img {
  width: 25rem;
  border-radius: 50%;
}

.profile::before {
  content: '';
  position: absolute;
  width: 408px;
  height: 408px;
  border: 5px solid #fff;
  border-radius: 50%;
}

.info-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 40rem;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: #fafafa;
  font-family: var(--homefont);
}

.info > .me {
  font-size: 4rem;
  font-weight: 900;
}

.name {
  font-size: 4.25rem;
  font-weight: 900;
  word-spacing: 0.5rem;
}

.fullstack,
.saa-c02 {
  font-size: 2rem;
  font-weight: 600;
}

.fullstack {
  word-spacing: 0.25rem;
}

.place {
  font-size: 1.85rem;
}
