* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.App {
  width: 100%;
  height: 100vh;
}

section {
  width: 100%;
}

section:nth-child(3) {
  width: 100%;
  scroll-snap-align: end;
}

section:nth-child(5) {
  width: 100%;
  scroll-snap-align: center;
}

.scrolled .nav {
  background: #fafafa;
  box-shadow: 0 1px 6px 3px #444;
  transition: all 300ms linear;
}

.scrolled .nav-items div {
  background: #191919;
  transition: all 300ms linear;
}

.scrolled .nav-items a {
  color: #fafafa;
}

.footer {
  color: #333;
  border-top: 4px solid #555;
  text-align: center;
  width: 95%;
  margin: auto;
  font-weight: 400;
}

/* Media queries */

/* for large devices */
@media only screen and (min-width: 800px) and (max-width: 1100px) {
  .home__container {
    flex-direction: column;
    height: auto;
    padding-top: 10rem;
    padding-bottom: 5rem;
  }

  .nav-items > div {
    margin-right: 0.75rem;
  }

  .profile {
    width: 100%;
  }

  .info-container {
    padding-top: 4rem;
    width: 100%;
  }

  .info {
    align-items: center;
    width: 100%;
  }

  .info .name {
    font-size: 3.5rem;
  }

  .info .fullstack {
    font-size: 1.75rem;
    padding-top: 0.5rem;
  }

  .info .saa-c02 {
    font-size: 1.65rem;
    padding-top: 0.5rem;
  }

  .info .place {
    padding-top: 0.5rem;
    font-size: 1.5rem;
  }
}

/* for mobile phones */
@media only screen and (max-width: 799px) {
  .nav {
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    transition: all 300ms linear;
  }

  .navActive {
    height: auto !important;
  }

  .nav-items {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-evenly;
  }

  .nav-items > div {
    background: #191919;
    padding: 0.5rem 0.6rem;
    border-radius: 0;
    color: #fff;
    font-size: 1rem;
    font-family: var(--homefont);
    font-weight: 800;
    filter: drop-shadow(0 0 0);
    transition: all 300ms linear;
  }

  .nav-items {
    display: none;
  }

  .navActive .nav-items {
    display: flex;
  }

  .nav-items > div > a {
    color: #fff;
  }

  .scrolled .nav-items > div > a {
    color: #000;
  }

  .toggleButton-wrapper {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }

  .nav-button {
    display: block;
    padding: 1rem;
  }

  .scrolled .toggle-nav {
    color: #000;
  }

  .scrolled .nav-items div {
    background: #fafafa;
    transition: all 300ms linear;
  }

  .navActive .nav-items div {
    display: block;
  }

  .home__container {
    flex-direction: column;
    align-items: center;
    height: auto;
    padding-top: 10rem;
    padding-bottom: 5rem;
  }

  .profile {
    width: 100%;
  }

  .profile > img {
    width: 20rem;
  }

  .profile::before {
    content: '';
    position: absolute;
    width: 332px;
    height: 332px;
    border: 5px solid #b5b5b9;
    border-radius: 50%;
  }

  .info-container {
    width: 100%;
  }

  .info {
    padding-top: 3rem;
    align-items: center;
    width: 100%;
  }

  .info .name {
    font-size: 3rem;
    font-weight: 800;
  }

  .info .fullstack {
    font-size: 1.5rem;
    padding-top: 0.5rem;
  }

  .info .saa-c02 {
    font-size: 1.5rem;
    padding-top: 0.5rem;
  }

  .info .place {
    padding-top: 0.5rem;
    font-size: 1.5rem;
  }

  .tab-content {
    height: auto;
    padding-top: 1rem;
    padding-bottom: 3rem;
  }

  .skills-container {
    justify-content: center;
  }

  .carousel {
    width: 80%;
  }

  .carousel_images img {
    width: 100%;
  }

  .card-container {
    padding-top: 1rem;
    padding-bottom: 2rem;
  }

  .card {
    flex-direction: column;
    align-items: center;
  }

  .card-image {
    width: 90%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;
  }

  .card-info {
    width: 90%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  .contact-container {
    flex-direction: column;
    padding-top: 2rem;
  }

  .social-img {
    order: 1;
    width: 80%;
    margin: auto;
  }

  .social-img > img {
    width: 100%;
  }

  .social {
    order: 2;
    width: 80%;
    margin: auto;
    padding: 1.5rem;
  }

  .social-icons {
    font-size: 3rem;
  }

  .email {
    font-size: 1.5rem;
  }

  .footer {
    order: 3;
  }
}

@media only screen and (max-width: 600px) {
  .info .name {
    font-size: 3.05rem;
  }

  .info .fullstack {
    font-size: 1.45rem;
  }

  .info .saa-c02 {
    font-size: 1.35rem;
  }

  .info .place {
    font-size: 1.25rem;
  }
}
