.project-container {
  background: #fafafa;
  width: 100%;
  padding-bottom: 3rem;
}

.project-heading {
  display: flex;
  justify-content: center;
  padding: 2rem 0;
  font-size: 2rem;
}

.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 80%;
  margin-bottom: 2rem;
  border-radius: 10px;
  box-shadow: 0 0 12px 1px rgb(64 60 67 / 40%);
}

.card-image {
  width: 50%;
  background: #fff;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  overflow: hidden;
}

.card-image .img-thumbnail-budget {
  width: 100%;
  transition: all 100ms linear;
}

.card-image > .img-thumbnail-budget:hover {
  transform: scale(1.2);
  transition: 100ms linear;
}

.card-image .img-thumbnail-todo {
  width: 120%;
  transition: all 100ms linear;
  padding: 2rem;
}

.card-image > .img-thumbnail-todo:hover {
  transform: scale(1.2);
  transition: 100ms linear;
}

.card p {
  margin-top: 0;
}

.card-info {
  width: 50%;
  background: #fff;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-name {
  font-size: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-tags {
  display: flex;
  flex-wrap: wrap;
  margin-top: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-tags p {
  padding: 10px 10px;
  background: #fbc403;
  margin-right: 1rem;
  border-radius: 4px;
}

.card-content {
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-links {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.view-link,
.github-link {
  background: #f5f5f5;
  padding: 0.75rem 1.75rem;
  border-radius: 6px;
  transition: all 150ms linear;
}

.card-links a {
  text-decoration: none;
  color: #555;
  transition: all 150ms linear;
}

.view-link:hover,
.github-link:hover {
  background: #ddd;
  transition: all 150ms linear;
}

.view-link:hover > a,
.github-link:hover > a {
  color: #111;
  transition: all 150ms linear;
}
