.tab-container {
  display: flex;
  justify-content: center;
}

.tablink {
  flex: 1;
  font-size: 1.25rem;
  border: none;
  outline: none;
  padding: 1rem 0;
  background: rgba(0, 0, 0, 0.9);
  color: #fafafa;
  font-family: var(--homefont);
  font-weight: 600;
}

.active {
  background: #fff;
  color: #000;
}

.tab-content {
  width: 100%;
  background: #fff;
  padding-bottom: 2rem;
}

