.accordian-container {
  position: relative;
  width: 100%;
  margin-bottom: 2rem;
}

.accordian {
  text-align: center;
  width: 100%;
  padding: 1.25rem 1.5rem;
  font-size: 1.15rem;
  border: none;
  cursor: pointer;
  outline: none;
  background: rgba(0, 0, 0, 0.9);
  color: #fff;
}

.accordian:hover {
  background: rgba(0, 0, 0);
}

.accordian span {
  position: absolute;
  right: 2.5%;
}

.activeAccordian {
  background-color: rgba(0, 0, 0, 0.9);
}

.activeAccordian:hover {
  background-color: rgba(0, 0, 0, 1);
}

