.panel {
  display: flex;
  align-items: center;
  height: 0px;
  overflow: hidden;
  transition: height 0.25s linear;
  background: rgba(255, 255, 255, 0.5);
}

.panel p {
  padding: 0 1.5rem;
}

.showPanel {
  height: 100%;
  transition: height 0.25s linear;
  background: rgba(255, 255, 255, 0.5);
}

.skill-tab {
  width: 90%;
  margin: auto;
  padding-top: 4rem;
  min-height: 30rem;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100%;
}

.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  width: 7rem;
  height: 7rem;
  background: #fff;
  border-radius: 50%;
}

.skills i {
  padding-top: 0.5rem;
  font-size: 3rem;
}

.skills p {
  margin: 0.5rem;
}

.skills i.fa-html5 {
  color: #ff6638;
}

.skills i.fa-css3-alt {
  color: #0084ff;
}

.skills i.fa-bootstrap {
  color: #7952b3;
}

.skills i.fa-sass {
  color: #ff80c1;
}

.skills i.fa-js-square {
  color: #f6bd00;
}

.skills i.fa-grunt {
  color: #f2a52e;
}

.skills i.fa-terminal {
  color: #952a33;
}

.skills i.fa-react {
  color: #5bccea;
}

.skills i.fa-node-js {
  color: #669b61;
}

.skills i.fa-gulp {
  color: #e44849;
}

.carousel {
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  width: 640px;
  margin: auto;
  padding-top: 3rem;
}

.carousel_images {
  display: flex;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}

.carousel_images img {
  width: 640px;
  object-fit: cover;
}

.carousel button {
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.75);
  outline: none;
  position: absolute;
  z-index: 1;
}

.carousel button:hover {
  border: 2px solid rgba(0, 0, 0, 0.7);
  cursor: pointer;
  box-shadow: 0 0 3px 1px rgba(88, 88, 88, 0.85);
}

.carousel button:active {
  box-shadow: 0 0 1px 0px rgba(88, 88, 88, 0.85);
}

.carousel button:hover i {
  color: rgba(0, 0, 0, 0.9);
}

.carousel i {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.75);
}

.tab-content .left {
  left: 5px;
}

.tab-content .right {
  right: 5px;
}
