.skill-icon {
  position: absolute;
  color: #fafafa;
  animation: scale-icons 1s linear forwards;
  opacity: 0;
}
.skill > div:nth-child(1) {
  top: -200px;
  left: -20px;
  font-size: 50px;
  filter: drop-shadow(0 2px 8px #585858);
  animation-delay: 0.125s;
}

