/* @import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap'); */
:root {
  --homefont: 'Roboto', sans-serif;
}

.home__container {
  display: flex;
  justify-content: center;
  height: 100vh;
  background: #191919;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  padding-top: 5rem;
}

