.profile {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40rem;
}

.profile > img {
  width: 25rem;
  border-radius: 50%;
}

.profile::before {
  content: '';
  position: absolute;
  width: 408px;
  height: 408px;
  border: 5px solid #fff;
  border-radius: 50%;
}

.info-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 40rem;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: #fafafa;
  font-family: 'Roboto', sans-serif;
  font-family: var(--homefont);
}

.info > .me {
  font-size: 4rem;
  font-weight: 900;
}

.name {
  font-size: 4.25rem;
  font-weight: 900;
  word-spacing: 0.5rem;
}

.fullstack,
.saa-c02 {
  font-size: 2rem;
  font-weight: 600;
}

.fullstack {
  word-spacing: 0.25rem;
}

.place {
  font-size: 1.85rem;
}

.nav {
  position: fixed;
  z-index: 1000;
  width: 100%;
  display: flex;
  height: 5rem;
  justify-content: flex-end;
  align-items: center;
  transition: all 400ms linear;
}

.nav-items {
  display: flex;
  flex-basis: 35%;
  justify-content: space-evenly;
}

.nav-items > div > a {
  text-decoration: none;
  color: #222;
}

.nav-items > div {
  background: #f1f1f1;
  padding: 0.5rem 0.6rem;
  border-radius: 10px;
  color: #222;
  font-size: 1rem;
  font-family: var(--homefont);
  font-weight: 800;
  -webkit-filter: drop-shadow(-2px 4px 1px #585858);
          filter: drop-shadow(-2px 4px 1px #585858);
}

.nav-button {
  display: none;
}

.toggle-nav {
  background: none;
  padding: 0;
  border: none;
  color: #fff;
  font-size: 1.25rem;
}

.tab-container {
  display: flex;
  justify-content: center;
}

.tablink {
  flex: 1 1;
  font-size: 1.25rem;
  border: none;
  outline: none;
  padding: 1rem 0;
  background: rgba(0, 0, 0, 0.9);
  color: #fafafa;
  font-family: var(--homefont);
  font-weight: 600;
}

.active {
  background: #fff;
  color: #000;
}

.tab-content {
  width: 100%;
  background: #fff;
  padding-bottom: 2rem;
}

/* .tab-content .me {
  display: flex;
