} */

.accordian-container {
  position: relative;
  width: 100%;
  margin-bottom: 2rem;
}

.accordian {
  text-align: center;
  width: 100%;
  padding: 1.25rem 1.5rem;
  font-size: 1.15rem;
  border: none;
  cursor: pointer;
  outline: none;
  background: rgba(0, 0, 0, 0.9);
  color: #fff;
}

.accordian:hover {
  background: rgba(0, 0, 0);
}

.accordian span {
  position: absolute;
  right: 2.5%;
}

.activeAccordian {
  background-color: rgba(0, 0, 0, 0.9);
}

.activeAccordian:hover {
  background-color: rgba(0, 0, 0, 1);
}

/* .accordian-container button:nth-child(1) {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
} */

.panel {
  display: flex;
  align-items: center;
  height: 0px;
  overflow: hidden;
  transition: height 0.25s linear;
  background: rgba(255, 255, 255, 0.5);
}

.panel p {
  padding: 0 1.5rem;
}

.showPanel {
  height: 100%;
  transition: height 0.25s linear;
  background: rgba(255, 255, 255, 0.5);
}

.skill-tab {
  width: 90%;
  margin: auto;
  padding-top: 4rem;
  min-height: 30rem;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100%;
}

.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  width: 7rem;
  height: 7rem;
  background: #fff;
  border-radius: 50%;
}

.skills i {
  padding-top: 0.5rem;
  font-size: 3rem;
}

.skills p {
  margin: 0.5rem;
}

.skills i.fa-html5 {
  color: #ff6638;
}

.skills i.fa-css3-alt {
  color: #0084ff;
}

.skills i.fa-bootstrap {
  color: #7952b3;
}

.skills i.fa-sass {
  color: #ff80c1;
}

.skills i.fa-js-square {
  color: #f6bd00;
}

.skills i.fa-grunt {
  color: #f2a52e;
}

.skills i.fa-terminal {
  color: #952a33;
}

.skills i.fa-react {
  color: #5bccea;
}

.skills i.fa-node-js {
  color: #669b61;
}

.skills i.fa-gulp {
  color: #e44849;
}

.carousel {
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  width: 640px;
  margin: auto;
  padding-top: 3rem;
}

.carousel_images {
  display: flex;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}

.carousel_images img {
  width: 640px;
  object-fit: cover;
}

.carousel button {
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.75);
  outline: none;
  position: absolute;
  z-index: 1;
}

.carousel button:hover {
  border: 2px solid rgba(0, 0, 0, 0.7);
  cursor: pointer;
  box-shadow: 0 0 3px 1px rgba(88, 88, 88, 0.85);
}

.carousel button:active {
  box-shadow: 0 0 1px 0px rgba(88, 88, 88, 0.85);
}

.carousel button:hover i {
  color: rgba(0, 0, 0, 0.9);
}

.carousel i {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.75);
}

.tab-content .left {
  left: 5px;
}

.tab-content .right {
  right: 5px;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 30rem;
  background: rgba(250, 251, 252, 0.9);
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 50%;
  height: 100%;
}

.social-img {
  width: 50%;
}

.social-img > img {
  width: 80%;
}

.social-icons {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  font-size: 3rem;
  color: #ec819f;
}

.social-icons div {
  margin: 1rem;
}

.connect {
  text-align: center;
}

.heading {
  font-size: 3rem;
  color: #35428b;
  font-weight: 600;
}

.email {
  color: #ec819f;
  font-size: 2rem;
}

.project-container {
  background: #fafafa;
  width: 100%;
  padding-bottom: 3rem;
}

.project-heading {
  display: flex;
  justify-content: center;
  padding: 2rem 0;
  font-size: 2rem;
}

.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 80%;
  margin-bottom: 2rem;
  border-radius: 10px;
  box-shadow: 0 0 12px 1px rgb(64 60 67 / 40%);
}

.card-image {
  width: 50%;
  background: #fff;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  overflow: hidden;
}

.card-image .img-thumbnail-budget {
  width: 100%;
  transition: all 100ms linear;
}

.card-image > .img-thumbnail-budget:hover {
  transform: scale(1.2);
  transition: 100ms linear;
}

.card-image .img-thumbnail-todo {
  width: 120%;
  transition: all 100ms linear;
  padding: 2rem;
}

.card-image > .img-thumbnail-todo:hover {
  transform: scale(1.2);
  transition: 100ms linear;
}

.card p {
  margin-top: 0;
}

.card-info {
  width: 50%;
  background: #fff;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-name {
  font-size: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-tags {
  display: flex;
  flex-wrap: wrap;
  margin-top: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-tags p {
  padding: 10px 10px;
  background: #fbc403;
  margin-right: 1rem;
  border-radius: 4px;
}

.card-content {
  margin-left: 1rem;
  margin-right: 1rem;
}

.card-links {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.view-link,
.github-link {
  background: #f5f5f5;
  padding: 0.75rem 1.75rem;
  border-radius: 6px;
  transition: all 150ms linear;
}

.card-links a {
  text-decoration: none;
  color: #555;
  transition: all 150ms linear;
}

.view-link:hover,
.github-link:hover {
  background: #ddd;
  transition: all 150ms linear;
}

.view-link:hover > a,
.github-link:hover > a {
  color: #111;
  transition: all 150ms linear;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.App {
  width: 100%;
  height: 100vh;
}

section {
  width: 100%;
}

section:nth-child(3) {
  width: 100%;
  scroll-snap-align: end;
}

section:nth-child(5) {
  width: 100%;
  scroll-snap-align: center;
}

.scrolled .nav {
  background: #fafafa;
  box-shadow: 0 1px 6px 3px #444;
  transition: all 300ms linear;
}

.scrolled .nav-items div {
  background: #191919;
  transition: all 300ms linear;
}

.scrolled .nav-items a {
  color: #fafafa;
}

.footer {
  color: #333;
  border-top: 4px solid #555;
  text-align: center;
  width: 95%;
  margin: auto;
  font-weight: 400;
}

/* Media queries */

/* for large devices */
@media only screen and (min-width: 800px) and (max-width: 1100px) {
  .home__container {
    flex-direction: column;
    height: auto;
    padding-top: 10rem;
    padding-bottom: 5rem;
  }

  .nav-items > div {
    margin-right: 0.75rem;
  }

  .profile {
    width: 100%;
  }

  .info-container {
    padding-top: 4rem;
    width: 100%;
  }

  .info {
    align-items: center;
    width: 100%;
  }

  .info .name {
    font-size: 3.5rem;
  }

  .info .fullstack {
    font-size: 1.75rem;
    padding-top: 0.5rem;
  }

  .info .saa-c02 {
    font-size: 1.65rem;
    padding-top: 0.5rem;
  }

  .info .place {
    padding-top: 0.5rem;
    font-size: 1.5rem;
  }
}

/* for mobile phones */
@media only screen and (max-width: 799px) {
  .nav {
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    transition: all 300ms linear;
  }

  .navActive {
    height: auto !important;
  }

  .nav-items {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-evenly;
  }

  .nav-items > div {
    background: #191919;
    padding: 0.5rem 0.6rem;
    border-radius: 0;
    color: #fff;
    font-size: 1rem;
    font-family: var(--homefont);
    font-weight: 800;
    -webkit-filter: drop-shadow(0 0 0);
            filter: drop-shadow(0 0 0);
    transition: all 300ms linear;
  }

  .nav-items {
    display: none;
  }

  .navActive .nav-items {
    display: flex;
  }

  .nav-items > div > a {
    color: #fff;
  }

  .scrolled .nav-items > div > a {
    color: #000;
  }

  .toggleButton-wrapper {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }

  .nav-button {
    display: block;
    padding: 1rem;
  }

  .scrolled .toggle-nav {
    color: #000;
  }

  .scrolled .nav-items div {
    background: #fafafa;
    transition: all 300ms linear;
  }

  .navActive .nav-items div {
    display: block;
  }

  .home__container {
    flex-direction: column;
    align-items: center;
    height: auto;
    padding-top: 10rem;
    padding-bottom: 5rem;
  }

  .profile {
    width: 100%;
  }

  .profile > img {
    width: 20rem;
  }

  .profile::before {
    content: '';
    position: absolute;
    width: 332px;
    height: 332px;
    border: 5px solid #b5b5b9;
    border-radius: 50%;
  }

  .info-container {
    width: 100%;
  }

  .info {
    padding-top: 3rem;
    align-items: center;
    width: 100%;
  }

  .info .name {
    font-size: 3rem;
    font-weight: 800;
  }

  .info .fullstack {
    font-size: 1.5rem;
    padding-top: 0.5rem;
  }

  .info .saa-c02 {
    font-size: 1.5rem;
    padding-top: 0.5rem;
  }

  .info .place {
    padding-top: 0.5rem;
    font-size: 1.5rem;
  }

  .tab-content {
    height: auto;
    padding-top: 1rem;
    padding-bottom: 3rem;
  }

  .skills-container {
    justify-content: center;
  }

  .carousel {
    width: 80%;
  }

  .carousel_images img {
    width: 100%;
  }

  .card-container {
    padding-top: 1rem;
    padding-bottom: 2rem;
  }

  .card {
    flex-direction: column;
    align-items: center;
  }

  .card-image {
    width: 90%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;
  }

  .card-info {
    width: 90%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  .contact-container {
    flex-direction: column;
    padding-top: 2rem;
  }

  .social-img {
    order: 1;
    width: 80%;
    margin: auto;
  }

  .social-img > img {
    width: 100%;
  }

  .social {
    order: 2;
    width: 80%;
    margin: auto;
    padding: 1.5rem;
  }

  .social-icons {
    font-size: 3rem;
  }

  .email {
    font-size: 1.5rem;
  }

  .footer {
    order: 3;
  }
}

@media only screen and (max-width: 600px) {
  .info .name {
    font-size: 3.05rem;
  }

  .info .fullstack {
    font-size: 1.45rem;
  }

  .info .saa-c02 {
    font-size: 1.35rem;
  }

  .info .place {
    font-size: 1.25rem;
  }
}

