.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 30rem;
  background: rgba(250, 251, 252, 0.9);
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 50%;
  height: 100%;
}

.social-img {
  width: 50%;
}

.social-img > img {
  width: 80%;
}

.social-icons {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  font-size: 3rem;
  color: #ec819f;
}

.social-icons div {
  margin: 1rem;
}

.connect {
  text-align: center;
}

.heading {
  font-size: 3rem;
  color: #35428b;
  font-weight: 600;
}

.email {
  color: #ec819f;
  font-size: 2rem;
}
